*{
    text-decoration: none;
}
.jumbotron{
    background-color: #ff3f34;
    color:#fff;
    padding: 5% 0;
    text-align: center;
}
.searcBtn{
    font-size: 2rem;
}
.heading{
    font-size: 10vw;
}
.brand-icon{
    color:yellow;
    font-size: 10vw;
   margin:auto 20px;
   text-align: center;
}

.searcField{
    width:80%;
}

.singleCard{
     margin:20px 0;
     padding: 30px;
     box-shadow: .1rem .1rem .2rem black;
     border-radius: 10px;
    
}
.singleHeading{
    font-size: 5vw;
    text-align: center;
    margin:2rem 0;
    font-weight: bolder;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.singleIngradient{
    font-size: 1.7vw;
    color:rgba(3,3,3,.9);
    font-weight: 500;
    letter-spacing: 1px;
}
.singleSubH{
  font-size: 2.5vw;
  padding:10px;

}

.SingleImg{
    width: 100%;
    height: 100%;
    padding:10px;
    
}

/* for home cards */
.cardsContainer{
    display: flex;
    justify-content:center;
    align-items: center;
    flex-wrap: wrap;
}
.cards{
         flex-basis:20%;
         padding:10px;
         margin:10px 0;
      }

      .mainCard{
          width:300px;
      }

      
.know-more{
   
    font-size:2vw;
    text-decoration:none ;
    padding: 1.2rem 1rem;
    
}

.btn-responsive {
    white-space: normal !important;
    word-wrap: break-word;
}




.site-footer
{
  background-color:#26272b;
  padding:45px 0 20px;
  font-size:15px;
  line-height:24px;
  color:#737373;
}
.site-footer hr
{
  border-top-color:#bbb;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a
{
  color:#737373;
}
.site-footer a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none
}
.footer-links li
{
  display:block
}
.footer-links a
{
  color:#737373
}
.footer-links a:active,.footer-links a:focus,.footer-links a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
  width:40px;
  height:40px;
  line-height:40px;
  margin-left:6px;
  margin-right:0;
  border-radius:100%;
  background-color:#33353d
}
.copyright-text
{
  margin:0
}
@media (max-width:991px)
{
  .site-footer [class^=col-]
  {
    margin-bottom:30px
  }
}
@media (max-width:767px)
{
  .site-footer
  {
    padding-bottom:0
  }
  .site-footer .copyright-text,.site-footer .social-icons
  {
    text-align:center
  }
}
.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  margin-bottom:4px
}
.social-icons li.title
{
  margin-right:15px;
  text-transform:uppercase;
  color:#96a2b2;
  font-weight:700;
  font-size:13px
}
.social-icons a{
  background-color:#eceeef;
  color:#818a91;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  -webkit-transition:all .2s linear;
  -o-transition:all .2s linear;
  transition:all .2s linear
}
.social-icons a:active,.social-icons a:focus,.social-icons a:hover
{
  color:#fff;
  background-color:#29aafe
}
.social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}
.social-icons a.facebook:hover
{
  background-color:#3b5998
}
.social-icons a.twitter:hover
{
  background-color:#00aced
}
.social-icons a.linkedin:hover
{
  background-color:#007bb6
}
.social-icons a.dribbble:hover
{
  background-color:#ea4c89
}
@media (max-width:767px)
{
  .social-icons li.title
  {
    display:block;
    margin-right:0;
    font-weight:600
  }
}




@media (max-width:500px) {

    .searcBtn{
        font-size: 1.2rem;
    }
    
    .searcField{
        width:100%;
        padding: 0 50px;
    }

    
}


@media (max-width:663px) {
    .mainCard{
        width:80vw;
    }

}
